/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 0px;
  height: 10px;
  cursor: pointer; }

::-webkit-scrollbar-track {
  background-color: white;
  -webkit-border-radius: 5px;
  border-radius: 5px; }

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background: lightgray; }

.navbar-top {
